.homebg{
    position: relative;
    height: 100vh;
    width: 100%;
    background-image: url('../../assets/images/home.jpg');
    background-size: cover;
}

.homebg::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.65);
}

.homeCont{
    position: relative;
    display: inline-flex;
    align-items: center;
    height: 100%;
}
.homeCont_wrapper{
    color: white;
}
.homeCont_wrapper h1{
    text-align: left;
    line-height: 1.2;
}